<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.shadow {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 6rem 0;
  background: rgba(0, 0, 0, .8);
  z-index: 9999;
  overflow: auto;
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: #e1e1e2;
    &:hover {
      background-color: #cfcfd1;
    }
  }
}
.aboutme {
  width: 650px;
  margin: 0 auto;
  border: 1px solid #000;
  header {
    display: flex;
    padding: 20px;
    flex-flow: row nowrap;
    align-items: center;
    color: #e4e4e4;
    background-color: #243745;
    .nick-name {
      padding-right: 20px;
      margin-right: 30px;
      color: #e4e4e4;
      font-size: 30px;
      font-style: italic;
      text-align: right;
      border-right: 5px solid #7aae9b;
      .introduction {
        font-size: 16px;
        color: #dedede;
      }
    }
    .detail {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-between;
      flex-grow: 1;
      font-size: 13px;
      .right {
        display:  inline-block; 
        width:  70px;
        text-align: right;  
      }
      a {
        color: #e4e4e4;
      }
    }
  }
  .content {
    padding: 30px 20px;
    margin: 0 auto;
    background-color: #fff;
    .title {
      padding-bottom: 10px;
      font-size: 18px;
      border-bottom: 1px solid #7aae9b;
    }
    ul,
    ol {
      padding-left: 30px;
      margin-top: 20px;
      margin-bottom: 30px;
      color: #666;
      ul,
      ol {
        font-size: 13px;
      }
    }
    .skill {
      list-style: none;
      ul {
        font-size: 13px;
      }
    }
    .other {
      font-size: 13px;
    }
    li {
      margin: .5rem 0;
    }
  }
}
@media screen and (max-width: 600px) {
  .aboutme {
    width: 100%;
    header {
      padding: 20px 0;
      .nick-name {
        display: none;
      }
      .detail {
        display: flex;
        padding-left: 20px;
        margin-left: 20px;
        border-left: 5px solid #7aae9b;
        .social {
          margin-top: 20px;
        }
      }
    }
    .content {
      ol ol,
      ol ul {
        padding-left: 0;
        ul {
          padding-left: 0;
        }
      }
    }
  }
}
@media screen and (max-width: 400px) {
  .aboutme {
    width: 100%;
    header {
      padding: 20px 0;
      .nick-name {
        display: none;
      }
      .detail {
        display: block;
        padding-left: 20px;
        margin-left: 20px;
        border-left: 5px solid #7aae9b;
        .social {
          margin-top: 20px;
        }
      }
    }
    .content {
      ol ol,
      ol ul {
        padding-left: 0;
        ul {
          padding-left: 0;
        }
      }
    }
  }
}
</style>



